button.bg-warning {
  margin: 0.5rem;
  text-transform: uppercase;
  padding: 1rem 2.5rem;
  border-radius: 30px;
  color: white;
  font-weight: 700;
  border: none;
  transition: 0.5s;
  background: none;
  margin: 40px auto;
  display: flex;
  font-size: 18px;
  cursor: pointer;
  user-select: none;
  grid-row: 1;
  background-color: #f5a623;
  border-bottom: 3px solid #db8a00;
  /* box-shadow: 0 10px 4px -6px rgba(6, 7, 64, 0.5); */
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}
button.bg-warning:hover {
  background-color: #db8a00;
}
button.bg-warning:disabled {
  background-color: rgba(0, 0, 0, 0.12);
  border-bottom: 3px solid rgba(0, 0, 0, 0.14);
}
button:active {
  border-bottom: none;
  box-shadow: none;
}

.bg-warning {
  background-color: #f5a623;
}
